const burger = document.querySelector('.burger'),
	secondaryMenu = document.querySelector('.secondary-menu'),
	headerItem = document.querySelectorAll('.header-menu__item'),
	search = document.querySelector('.search'),
	headerContainer = document.querySelector('.container-header'),
	scrolled = document.querySelector('.container-header.scrolled'),
	activeMenu = document.querySelector('.secondary-menu.active'),
	logoBlack = document.querySelector('.logo-black'),
	logoFont = document.querySelector('.logo-font')

if (burger) {
	burgerScripts()
}

function burgerScripts() {
	const headerMenu = document.querySelector('.header-menu'),
		burgerInner = burger.querySelector('.burger__inner'),
		menuItem = document.querySelectorAll('.header-menu__item'),
		headerAbsolute = document.querySelector('.header-absolute')

	// const dropDown = document.querySelector('.dropdown'),
	//     dropItems = document.querySelector('.dropdown__items'),
	//     dropItem = document.querySelectorAll('.dropdown__item');

	burgerListener()
	burgerCloseKeyDown()
	menuItemIteration()

	// dropdownInBurger(dropItem, dropItems);

	function overflowMenu(header) {
		if (burgerInner.classList.contains('burger__inner--active')) {
			document.body.style.overflow = 'hidden'
			if (headerContainer) {
				headerContainer.style.borderBottom = 'none'
			}
			setTimeout(() => {
				if (secondaryMenu) {
					secondaryMenu.classList.add('active')
				} else if (headerContainer) {
					headerContainer.classList.add('container-header__active')
					headerContainer.style.background = 'none'
				}
			}, 200)
			header.forEach(el => {
				el.style.opacity = '0'
			})
			// headerAbsolute.style.zIndex = '0'
		} else {
			document.body.style.overflow = ''
			if (headerContainer) {
				headerContainer.style.borderBottom = ''
			}
			header.forEach(el => {
				el.style.opacity = ''
			})
			setTimeout(() => {
				if (secondaryMenu) {
					secondaryMenu.classList.remove('active')
				} else if (headerContainer) {
					headerContainer.classList.remove('container-header__active')
					headerContainer.style.background = ''
				}
				// headerAbsolute.style.zIndex = '30'
			}, 200)
		}
	}

	function removeMenuActive() {
		burgerInner.classList.remove('burger__inner--active')
		headerMenu.classList.remove('header-menu--active')

		overflowMenu(headerItem)
	}

	function burgerListener() {
		burger.addEventListener('click', () => {
			headerMenu.classList.toggle('header-menu--active')
			burgerInner.classList.toggle('burger__inner--active')

			// dropDown.classList.add('dropdown-burger');

			overflowMenu(headerItem)
		})
	}

	function burgerCloseKeyDown() {
		document.addEventListener('keydown', event => {
			if (
				event.code === 'Escape' &&
				burgerInner.classList.contains('burger__inner--active')
			) {
				removeMenuActive()
			}
		})
	}

	function menuItemIteration() {
		menuItem.forEach((item, i) => {
			item.addEventListener('click', () => {
				if (!menuItem[i].querySelector('.dropdown')) {
					removeMenuActive()
				}
			})
		})
	}
}

window.onscroll = function () {
	if (window.scrollY > 0 && !secondaryMenu.classList.contains('active')) {
		if (headerContainer) {
			headerContainer.classList.add('scrolled')
			logoFont.style.opacity = '1'
		}
		document.querySelector('.second-header').style.paddingBlock = '11.5px'
		logoBlack.classList.remove('active')
	} else if (secondaryMenu.classList.contains('active')) {
		document.querySelector('.logo-white').style.opacity = '1'
		logoFont.style.opacity = ''
	} else {
		if (headerContainer) {
			headerContainer.classList.remove('scrolled')
			logoFont.style.opacity = ''
		}
		document.querySelector('.second-header').style.paddingBlock = ''
		logoBlack.classList.add('active')
	}
}

burger.addEventListener('click', () => {
	if (document.querySelector('.burger__inner--active')) {
		if (headerContainer) {
			headerContainer.style.background = 'transparent'
		}
		if (logoBlack) {
			logoBlack.classList.remove('active')
		} else if (logoFont) {
			logoFont.style.opacity = '1'
		}
	} else if (!document.querySelector('.scrolled') && headerContainer) {
		headerContainer.style.background = '#e8edf0'
		if (logoBlack) {
			logoBlack.classList.add('active')
		}
	} else {
		if (headerContainer) {
			headerContainer.style.background = '#e8edf0'
		}
		logoFont.style.opacity = '1'
	}
})

burger.addEventListener('click', () => {
	if (
		document.querySelector('.logo-black.active') ||
		document.querySelector('.logo-font').style.opacity == '1'
	) {
		document.querySelector('.logo-white').style.opacity = ''
	} else {
		document.querySelector('.logo-white').style.opacity = '1'
	}
})
