const marketsTrigger = document.querySelector('.markets-trigger')
const marketsPanel = document.querySelector('.markets-panel')

if (marketsPanel) {
	marketsTrigger.addEventListener('click', function (e) {
		e.preventDefault()
		document.body.style.overflowY = 'hidden'
		marketsTrigger.classList.add('active')
		marketsPanel.classList.add('active')
	})

	marketsPanel.addEventListener('mouseleave', () => {
		marketsTrigger.classList.remove('active')
		marketsPanel.classList.remove('active')
		document.body.style.overflowY = ''
	})

	document.addEventListener('click', e => {
		if (
			!marketsTrigger.contains(e.target) &&
			!marketsPanel.contains(e.target)
		) {
			// Check if the clicked element is not marketsTrigger or marketsPanel
			if (
				marketsTrigger.classList.contains('active') ||
				marketsPanel.classList.contains('active')
			) {
				// Only remove the active class if it is currently added
				marketsTrigger.classList.remove('active')
				marketsPanel.classList.remove('active')
				document.body.style.overflowY = ''
			}
		}
	})
}
