let classesToKeep = []

const items = document.querySelectorAll('.production-item')
const cancelFilter = document.querySelectorAll(
	'.find-solution-inputs__other-cancel'
)
const linesBtn = document.querySelector('.lines')
const generalBtn = document.querySelector('.general')
const regularRow = document.querySelector('.production__row--regular')
const mobRow = document.querySelector('.production__row--mob')
const linesRow = document.querySelectorAll('.production__row--lines')
const rangeDelay = document.getElementById('rangeDelay')
const rangeIntensity = document.getElementById('rangeIntensity')
const bugsFilters = [
	'mosquito',
	'midges',
	'gnat',
	'mosquitoes',
	'gadflies',
	'fleas',
	'lice',
	'mites'
]

const peopleFilters = ['children', 'adults', 'family']

if (document.querySelector('.find-solution')) {
	const filterItems = () => {
		items.forEach(item => {
			item.classList.remove('hide')
			let found = classesToKeep.some(cls => item.classList.contains(cls))
			if (!found) {
				item.classList.add('hide')
			}
		})
	}

	function createHandler(className) {
		return () => {
			const isBugsSelect = bugsFilters.includes(className)
			const isPeopleSelect = peopleFilters.includes(className)

			if (isBugsSelect) {
				classesToKeep = [className]
			} else if (isPeopleSelect) {
				classesToKeep = [className]
			} else {
				if (classesToKeep.indexOf(className) < 0) {
					classesToKeep.push(className)
				} else {
					classesToKeep = classesToKeep.filter(cls => cls !== className)
				}
			}

			filterItems()
		}
	}

	const buttons = [
		{ selector: '.find-solution-panel__children', className: 'children' },
		{ selector: '.find-solution-panel__family', className: 'family' },
		{ selector: '.find-solution-panel__adult', className: 'adults' },
		{ selector: '.find-solution-panel__mosquito', className: 'mosquito' },
		{ selector: '.find-solution-panel__midges', className: 'midges' },
		{ selector: '.find-solution-panel__gnat', className: 'gnat' },
		{ selector: '.find-solution-panel__mosquitoes', className: 'mosquitoes' },
		{ selector: '.find-solution-panel__gadflies', className: 'gadflies' },
		{ selector: '.find-solution-panel__fleas', className: 'fleas' },
		{ selector: '.find-solution-panel__lice', className: 'lice' },
		{ selector: '.find-solution-panel__mites', className: 'mites' },
		{
			selector: '.find-solution-inputs__other-anti-allergenic',
			className: 'anti-allergenic'
		}
	]

	buttons.forEach(({ selector, className }) => {
		const button = document.querySelector(selector)
		button.addEventListener('click', function () {
			generalBtn.classList.remove('active')
			let handler = createHandler(className)
			handler()

			if (bugsFilters.includes(className)) {
				buttons
					.filter(
						({ className: otherClassName }) =>
							bugsFilters.includes(otherClassName) &&
							otherClassName !== className
					)
					.forEach(({ selector }) => {
						const button = document.querySelector(selector)
						let innerDiv = button.querySelector('div')
						let innerSvg = button.querySelector('svg')
						if (innerDiv) {
							innerDiv.style.color = ''
							innerSvg.style.fill = ''
						}
					})
			} else if (peopleFilters.includes(className)) {
				buttons
					.filter(
						({ className: otherClassName }) =>
							peopleFilters.includes(otherClassName) &&
							otherClassName !== className
					)
					.forEach(({ selector }) => {
						const button = document.querySelector(selector)
						let innerDiv = button.querySelector('div')
						let innerSvg = button.querySelector('svg')
						if (innerDiv) {
							innerDiv.style.color = ''
							innerSvg.style.fill = ''
						}
					})
			}

			if (linesBtn.classList.contains('active')) {
				linesBtn.classList.remove('active')
				generalBtn.classList.add('active')
				regularRow.style.opacity = ''
				regularRow.style.display = ''
				mobRow.style.opacity = ''
				mobRow.style.display = ''
				classesToKeep = [] // reset the filters
				buttons.forEach(({ selector }) => {
					const button = document.querySelector(selector)
					let innerDiv = button.querySelector('div')
					let innerSvg = button.querySelector('svg')
					if (innerDiv) {
						innerDiv.style.color = ''
						innerSvg.style.fill = ''
					}
				})
				items.forEach(item => item.classList.remove('hide'))
				linesRow.forEach(item => {
					item.style.opacity = '0'
					item.style.display = 'none'
					item.classList.add = 'hide'
				})
			}

			if (button.classList.contains('not-input-red')) {
				let innerDiv = button.querySelector('div')
				let innerSvg = button.querySelector('svg')
				if (innerDiv) {
					if (innerDiv.style.color === 'rgb(223, 17, 37)') {
						innerDiv.style.color = ''
						innerSvg.style.fill = ''
					} else {
						innerDiv.style.color = 'rgb(223, 17, 37)'
						innerSvg.style.fill = 'rgb(223, 17, 37)'
					}
				}
			}

			if (button.classList.contains('not-input-green')) {
				let innerDiv = button.querySelector('div')
				let innerSvg = button.querySelector('svg')
				if (innerDiv) {
					if (innerDiv.style.color === 'rgb(33, 150, 83)') {
						innerDiv.style.color = ''
						innerSvg.style.fill = ''
					} else {
						innerDiv.style.color = 'rgb(33, 150, 83)'
						innerSvg.style.fill = 'rgb(33, 150, 83)'
					}
				}
			}
		})
	})

	cancelFilter.forEach(item => {
		item.addEventListener('click', function () {
			generalBtn.classList.add('active')
			if (linesBtn.classList.contains('active')) {
				linesBtn.classList.remove('active')
				linesRow.forEach(item => {
					item.style.opacity = ''
					item.style.display = ''
				})
				regularRow.style.opacity = ''
				regularRow.style.display = ''
			}
			classesToKeep = []
			buttons.forEach(({ selector }) => {
				const button = document.querySelector(selector)
				let innerDiv = button.querySelector('div')
				let innerSvg = button.querySelector('svg')
				if (innerDiv) {
					innerDiv.style.color = ''
					innerSvg.style.fill = ''
				}
			})
			items.forEach(item => item.classList.remove('hide'))

			if (linesRow.style.display === 'grid') {
				linesRow.style.display = 'none'
				regularRow.style.display = 'block'
			}
		})
	})

	linesBtn.addEventListener('click', function () {
		if (!linesBtn.classList.contains('active')) {
			linesBtn.classList.add('active')
			generalBtn.classList.remove('active')
			regularRow.style.opacity = '0'
			regularRow.style.display = 'none'
			regularRow.classList.add = 'hide'
			mobRow.style.opacity = '0'
			mobRow.style.display = 'none'
			mobRow.classList.add = 'hide'
			classesToKeep = []
			buttons.forEach(({ selector }) => {
				const button = document.querySelector(selector)
				let innerDiv = button.querySelector('div')
				let innerSvg = button.querySelector('svg')
				if (innerDiv) {
					innerDiv.style.color = ''
					innerSvg.style.fill = ''
				}
			})
			items.forEach(item => item.classList.remove('hide'))
			linesRow.forEach(item => {
				item.style.opacity = '1'
				item.style.display = 'grid'
				item.classList.remove = 'hide'
			})
		}
	})

	generalBtn.addEventListener('click', function () {
		if (linesBtn.classList.contains('active')) {
			linesBtn.classList.remove('active')
			generalBtn.classList.add('active')
			regularRow.style.opacity = ''
			regularRow.style.display = ''
			mobRow.style.opacity = ''
			mobRow.style.display = ''
			classesToKeep = []
			buttons.forEach(({ selector }) => {
				const button = document.querySelector(selector)
				let innerDiv = button.querySelector('div')
				let innerSvg = button.querySelector('svg')
				if (innerDiv) {
					innerDiv.style.color = ''
					innerSvg.style.fill = ''
				}
			})
			items.forEach(item => item.classList.remove('hide'))
			linesRow.forEach(item => {
				item.style.opacity = '0'
				item.style.display = 'none'
				item.classList.add = 'hide'
			})
		}
	})

	function generateInputRange(one, two, numPoints, range, classes) {
		const inputRange = document.getElementById(range)

		if (inputRange) {
			const three = (two - one) / (numPoints - 1)

			inputRange.setAttribute('min', one)
			inputRange.setAttribute('max', two)
			inputRange.setAttribute('step', three)
			inputRange.setAttribute('value', one)

			inputRange.oninput = () => {
				let val = +inputRange.value
				classesToKeep = classesToKeep.filter(cls => !classes.includes(cls))
				const newFilter = classes[val]

				if (bugsFilters.includes(newFilter)) {
					classesToKeep = classesToKeep.filter(
						cls => !bugsFilters.includes(cls)
					)
				} else if (peopleFilters.includes(newFilter)) {
					classesToKeep = classesToKeep.filter(
						cls => !peopleFilters.includes(cls)
					)
				}

				classesToKeep.push(newFilter)

				filterItems()

				if (val == 0) {
					inputRange.className = 'green'
				} else if (val == 1) {
					inputRange.className = 'orange'
				} else if (val == 2) {
					inputRange.className = 'red'
				}
			}

			const texts = ['1-2 часа', '2-3 часа', '5 часов']

			const divs = Array.from(
				inputRange.parentNode.querySelectorAll(
					'.find-solution-inputs__points div'
				)
			)

			divs.forEach(function (div, index) {
				div.addEventListener('click', function () {
					inputRange.value = index
					inputRange.dispatchEvent(new Event('input'))
				})
			})

			return inputRange
		}
	}

	const startValue = 0
	const endValue = 2
	const numPoints = 3

	const delay = generateInputRange(
		startValue,
		endValue,
		numPoints,
		'rangeDelay',
		['one', 'two', 'three']
	)
	const intensity = generateInputRange(
		startValue,
		endValue,
		numPoints,
		'rangeIntensity',
		['low', 'medium', 'high']
	)
}
