import gsap from 'gsap'

const production = document.querySelector('#production')
const lines = document.querySelector('#lines')

gsap.to(production, {
	rotation: 45, // нужный угол вращения на каждом шаге в градусах
	stagger: 0.3, // задержка между анимацией объектов в коллекции в секундах
	transformOrigin: 'left', // ориентационная точка (как будто шарнир)
	opacity: 1, // конечная прозрачность элементов
	duration: 0.3, // продолжительность анимации каждого элемента в секундах
	ease: 'power1.inOut', // тип анимирования (для smooth анимации используйте ease)
})
