// Описание переменных.

const sliderItems = document.querySelectorAll('.slider-item')
const paginationItems = document.querySelectorAll('.pagination li')
const navItems = document.querySelectorAll('.nav li')
const anchorLink = document.querySelectorAll('.anchor-link')
const pointToScroll = document.querySelector('.product-selection-slider')

// Функция для смены слайда.
function changeSlide(index) {
	// Убрать активный класс.
	sliderItems.forEach(item => item.classList.remove('active'))
	paginationItems.forEach(item => item.classList.remove('active'))
	navItems.forEach(item => item.classList.remove('active'))

	// Добавить активный класс.
	sliderItems[index].classList.add('active')
	paginationItems[index].classList.add('active')
	navItems[index].classList.add('active')
}

paginationItems.forEach((item, index) => {
	item.addEventListener('click', () => changeSlide(index))
})

navItems.forEach((item, index) => {
	item.addEventListener('click', () => changeSlide(index))
})

anchorLink.forEach(item => {
	item.addEventListener('click', () => {
		pointToScroll.scrollIntoView({ behavior: 'smooth' })
	})
})
