import Swiper, { EffectFade, Navigation, Pagination, Scrollbar } from 'swiper'

let titles = ['FAMILY', 'BABY', 'EXTREME', 'NATURAL', 'THERAPY']
let productSelection = document.querySelector('.product-selection-slider')

if (productSelection) {
	const selection = new Swiper('.product-selection-slider', {
		modules: [Pagination, EffectFade],
		// effect: 'fade',
		pagination: {
			el: '.pagination',
			clickable: true,
			renderBullet: function (index, className) {
				return '<div class="' + className + '">' + titles[index] + '</div>'
			},
		},
	})

	document.getElementById('family').addEventListener('click', function (event) {
		event.preventDefault()
		selection.slideTo(0, 1000, false)
	})

	document
		.getElementById('extreme')
		.addEventListener('click', function (event) {
			event.preventDefault()
			selection.slideTo(2, 1000, false)
		})

	document
		.getElementById('natural')
		.addEventListener('click', function (event) {
			event.preventDefault()
			selection.slideTo(3, 1000, false)
		})

	document.getElementById('baby').addEventListener('click', function (event) {
		event.preventDefault()
		selection.slideTo(1, 1000, false)
	})

	document
		.getElementById('therapy')
		.addEventListener('click', function (event) {
			event.preventDefault()
			selection.slideTo(4, 1000, false)
		})

	const extreme = new Swiper('.product-selection-extreme-slider', {
		nested: true,
		modules: [Navigation, Pagination, Scrollbar],
		slidesPerView: 4,
		navigation: {
			nextEl: '.swiper-button-next',
			prevEl: '.swiper-button-prev',
		},
		pagination: {
			el: '.swiper-pagination',
			type: 'fraction',
		},
		scrollbar: {
			el: '.swiper-scrollbar',
			hide: false,
		},
	})
}
